:root {
  --borders : #ddd;
  --callPadding: .2rem .2rem .2rem .2rem;
  --background1 : #f8f8f8;
  --background2 : #f2f2f2;
  --background3 : rgba(255,200,200,0.2);;
  --bodyBackground : #efefef;
  --playbtnColor : red;
  --progressBgColor : #f8f8f8;
  --progressBarColor : red;
}


.btn, .form-control {
  font-size:.8rem !important;
}
body {
  margin: 0;
  font-family: 'Poppins',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size:.8rem;
}

h5 {font-size:1.1rem !important}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn-add {
  margin-left: 5px;
}

.page-title {font-size: 1.25rem !important;}

.footer {
  width:100%;
  background-color:rgba(0,0,0,.8);
  color:#dadada;
  position:absolute;
  bottom:0;
  z-index: 9999;
  padding: 5px;
}

.modal-header, .modal-body, .modal-footer {
  padding:.5rem !important;
}

.text-bg-secondary {
  background-color: rgba(0,0,0,.8) !important;
}

@media screen and (max-width: 768px) 
{
  .btn-add {
    margin-left: 0px;
    margin-top : 10px;
  }
  .page-title { text-align: center; width:100vw}
  
}

/* 768 */

/* grid */
table {
  border: 1px solid var(--borders);
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  width: 100%;
  table-layout:auto;
  margin-top:20px;
}
table th, table td {
  padding: .5em;
}

table th {
  height: 60px;
}


table tr {
  background-color: var(--background1);
  border: 1px solid  var(--borders);
  padding: .35em;
}

table th {
  text-align: center;
  vertical-align:middle;
}
table td {
  text-align: left;
  vertical-align: top;
  font-size: .9em !important;
}
table td:nth-child(1) { width: 80px;}
table td:nth-child(2) { min-width: 400px;}
table td:nth-child(3) { min-width: 300px;}
table td:nth-child(4) { width: 200px; text-align: right;}
table td:nth-child(5) { width: 300px; text-align: right;}
table td:nth-child(6) { width: 60px;}
table td:nth-child(7) { min-width: 100px;}
table tr:nth-child(even) td {background-color: var(--background2);}
table tr:hover td {background-color: var(--background3)}
table th {
  font-size: .85em;
  letter-spacing: .1em;
  text-transform: uppercase;
}
.cover { 
  width: 160px; 
  border:2px solid var(--background2); 
  margin-left:10px; 
  margin-right:10px; 
  margin-bottom:10px; 
  box-shadow: 2px 2px 5px rgba(0,0,0,.2);
}

.paging {display:flex; justify-content: end; padding: .9em}
.page {width:30px; font-size:.85em}
.content {display:flex; justify-content: flex-start; gap:2px} 

.ptitle { display: none;}
/* end of grid*/

.body-grid {
  margin-top: -5px;
}
/* Responsive Section */
@media screen and (max-width: 768px) 
{
  .body-grid {
    margin-top: 10px;
  }

  .content {display:flex; justify-content: space-between;} 
  .ptitle { display: block;}
  table {
    border: 0;
    margin-top:20px;
    margin-left:10px;
    margin-right:10px;
    width:calc(100% - 20px);
  }

  table td:nth-child(1) {
    display: none;
  }

  table td:nth-child(1),
  table td:nth-child(2),
  table td:nth-child(3),
  table td:nth-child(4),
  table td:nth-child(5),
  table td:nth-child(6),
  table td:nth-child(7)
  { width: auto;}
  table tr:nth-child(even) td {background-color: var(--background1);}
  table tr:hover td {background-color: transparent;}
  table tr:hover {background-color: var(--background3)}
  
  table thead {
    border: none;
    /* clip: rect(0 0 0 0); */
    /* height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute; */
    display:none;
    /* width: 1px; */
  }
  
  table tr {
    border-bottom: 3px solid #ddd;
    display: block;
    margin-bottom: .625em;
  }
  
  table td {
    border-bottom: 1px solid #ddd;
    display: block;
    font-size: .8em;
    text-align: right;
  }
  
  table td::before {
    /* content: attr(data-label); */
    float: left;
    font-weight: bold;
    text-transform: uppercase;
  }
  
  table td:last-child {
    border-bottom: 0;
  }
}